    .pt-datepicker .DayPicker-NavButton--next {
      right: -5px; }
      .pt-datepicker .DayPicker-NavButton--next::before {
        line-height: 1;
        font-family: "Icons16", sans-serif;
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        -moz-osx-font-smoothing: grayscale;
