        content: ""; }
  .pt-datepicker .DayPicker-Weekdays {
    display: table-header-group; }
  .pt-datepicker .DayPicker-WeekdaysRow {
    display: table-row; }
  .pt-datepicker .DayPicker-Weekday {
    display: table-cell;
